@mixin minifiedmobile {
  @media (max-width: 400px){
    @content;
  }
}
@mixin minimobile {
  @media (max-width: 450px){
    @content;
  }
}
@mixin mobile {
  @media (max-width: 738px){
    @content;
  }
}


@mixin tablet {
  @media (max-width: 1366px){
    @content;
  }
}


@mixin desktop {
  @media (max-width: 1366px){
    @content;
  }
}

